body {
  background: white;
  color: #121212;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", sans-serif;
}

/* Components */
.btn {
  padding: 16px 40px;
  gap: 10px;
  width: 128px;
  height: 48px;
  border-radius: 32px;
  border: none;
  background-color: #E4C1AE;
  color: white;
  font-weight: 700;
  cursor: pointer;
}
.btn:hover {
  background-color: #ffbf9d;
}

.img-sea {
  width: 505px;
  height: 330px;
  object-fit: fill;
}

@media (max-width: 767px) {
  .img-sea {
    width: auto;
  }
}
.card-body .card-title {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 42px;
  color: #121212;
}
.card-body .card-description {
  width: 380px;
  height: 96px;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  color: #121212;
  margin: 24px 0 32px 0;
}

/* Sections */
.container {
  max-width: 1140px;
  height: 960px;
  margin: 96px 230px 0 230px;
}

article {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  gap: 95px;
}

@media (max-width: 767px) {
  article {
    flex-wrap: wrap;
  }
}
/* Views / Pages */